@-webkit-keyframes wrong {
    0%, 100% {
        margin-left: 0;
    }
    20%, 60% {
        margin-left: -30px;
    }
    40%, 80% {
        margin-left: 30px;
    }
}

@keyframes wrong {
    0%, 100% {
        margin-left: 0;
    }
    20%, 60% {
        margin-left: -30px;
    }
    40%, 80% {
        margin-left: 30px;
    }
}

.wrong {
    -webkit-animation: wrong 0.3s;
            animation: wrong 0.3s;
}


